.team {
  &__inner-img {
    margin-bottom: 43px;
  }

  &__content-title {
    position: relative;
    text-align: center;
    color: #4A4A4A;
    margin-bottom: 51px;

    &::after {
      content: "";
      position: absolute;
      width: 100px;
      height: 8px;
      background-color: #fff;
      top: 0px;
      left: 0;
      right: 0;
      margin: 0 auto;
      bottom: 0;
    }
  }
}

